*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.mainClass{
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 90vh;
    overflow: hidden;
}

.container {
    position: relative;
    width: 1050px;
    display: flex;
    justify-content: space-evenly;
    transform: skewY(-10deg);
}

.container .card {
    position: relative;
    width: 300px;
    transition: .5s;
}

.container .card:before {
    content: '';
    position: absolute;
    top: -15px;
    left: 0;
    width: 100%;
    height: 15px;
    background: #00c0f6;
    transform-origin: bottom;
    transform: skewX(45deg);
}

.container .card:after {
    content: '';
    position: absolute;
    top: -15px;
    left: -15px;
    width: 15px;
    height: 50%;
    background: #00c0f6;
    transform-origin: left;
    transform: skewY(45deg);
    border-bottom: 200px solid #d9d9d9
}

.container .card:hover {
    transform: translateY(-40px);
}

.container .card .imgBx {
    height: 200px;
    background-color: #00c7ff;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

}

.container .card .imgBx img{
    max-width: 100px;
}

.container .card .imgBx h3{
    color: #fff;
    margin-top: 10px;
}

.container .card .content {
    height: 200px;
    padding: 20px;
    color: #777;
    background: #fff;
}

.container .card .content:before {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 400px;
    background: linear-gradient(transparent, transparent, rgba(0,0,0,.1));
    transform-origin: bottom;
    transform: skewX(45deg);
    transition: .5s;
    pointer-events: none;
    z-index: -1;
}

.container .card:hover .content:before {
    transform: translate(-40px) skewX(45deg);
    filter: blur(5px);
    opacity: .5;
}

.container .card:nth-child(1) {
    z-index: 3;
}

.container .card:nth-child(2) {
    z-index: 2;
}

.container .card:nth-child(3) {
    z-index: 1;
}